.sidebar{
	grid-area: sidebar;
	background-color: green;
}
.navbar{
	margin: 20px 0px;
	font-size: 25px;
	font-weight: 700;
}
.nav{
	margin: 10px 0px;
}
.nav a{
	color: #fff;
	transition: ease-out 0.3s;
}
.nav a:hover{
	color: #fff;
	transition: ease-out 0.3s;
	border-bottom: 3px solid #fff;
}
.active{
	color: #fff;
	border-bottom: 3px solid #fff;
}
.friendsName{
	display: flex;
}
.friends{
	text-align: center;
	color: #ffffff;
	font-weight: 600;
	font-size: smaller;
}
.round{
	width: 50px;
	height: 50px;
	border-radius: 50%;
	margin-right: 10px;
	background-color: orange;
	text-align: center;
}