.content{
	display: grid;
	max-width: 1200px;
	width: 100%;
	margin: 0 auto;
	grid-template-areas:
	"header header"
	"sidebar container"
	"footer footer";
	grid-template-rows: 60px 8fr 100px;
	grid-template-columns: 3fr 9fr;
	grid-gap: 10px;
	background-color: #e8e8e8;
	font-family: sans-serif;
}
li{
	list-style: none;
}
.container{
	grid-area: container;
}
a{
	text-decoration: none;
}