.postsInput button{
    float: right;
    margin-top: 15px;
    width: 100px;
    height: 40px;
    background: green;
    color: #fff;
    border: none;
}
.postsInput button:hover{
    background: darkgreen;
}
.postsList{
    margin-top: 100px;
}
.posts{
    margin: 20px 0px;
    font-size: 17px;
    font-weight: 500px;
    display: flex;
}
.posts img{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right: 10px;
}
.selected{
    font-weight: bolder;
    font-size: 20px;
    padding: 10px;
}
.pagination{
    margin: 10px;
}
.pageNumber{
    padding: 2px;
    border: 1px solid grey;
}
.pageNumber.selectedPage{
    font-weight: bold;
    border-color: black;
}